/* Global */
[data-theme="claudiondark"] {
  --main-color: #000000;
  --second-color: #1cdeb0;
  --third-color: #bcbec0;
  --fourth-color: #bcbec0;
  --fifth-color: #bcbec0;
  --sixth-color: #ffffff;

  --primary: var(--main-color);
  --dt-primary-color: var(--main-color);
  --blue-avatar-bg: var(--main-color);
  --blue-avatar-color: var(--main-color);
  --blue: var(--main-color);

  --navbar-bg: var(--second-color);
  --fg-hover-color: var(--fifth-color);
  --card-bg: var(--main-color);
  --control-bg: var(--fifth-color);
  --control-bg-on-pink: var(--third-color);
}

/* Page Background */
[data-theme="claudiondark"] .page-container,
[data-theme="claudiondark"] .layout-main-section {
  background-color: var(--second-color);
  border: none !important;
}

/* Links */
[data-theme="claudiondark"] ul li a:hover {
  background-color: var(--second-color);
  color: var(--sixth-color);
}

/* Main Body */
[data-theme="claudiondark"] body {
  font-size: 14px;
  background-color: var(--fourth-color);
}

[data-theme="claudiondark"] a {
  color: var(--fifth-color);
  font-weight: 500;
}
[data-theme="claudiondark"] a.ellipsis {
  color: #063c44;
}

[data-theme="claudiondark"] .small,
small {
  font-size: 95%;
}

[data-theme="claudiondark"] .h6.uppercase,
h6.uppercase {
  font-weight: 600;
  font-size: 11.5px;
}

[data-theme="claudiondark"] h1#sellingsettings {
  color: var(--fourth-color);
}

[data-theme="claudiondark"] p {
  color: var(--fourth-color);
}

[data-theme="claudiondark"] li {
  color: var(--fourth-color);
}
[data-theme="claudiondark"] use {
  fill: var(--fourth-color);
}

/* Navigation Bar */

[data-theme="claudiondark"] .sidebar-item-label {
  color: var(--fourth-color);
}
[data-theme="claudiondark"] .sidebar-label {
  color: var(--fourth-color);
}
[data-theme="claudiondark"] .standard-sidebar-item {
  color: var(--fourth-color);
}
[data-theme="claudiondark"] .standard-sidebar-label {
  color: var(--fourth-color);
}

[data-theme="claudiondark"] .standard-sidebar-item.selected {
  background: var(--second-color);
}

[data-theme="claudiondark"] .standard-sidebar-item.selected a {
  color: var(--sixth-color);
}

[data-theme="claudiondark"] .standard-sidebar-item.selected a svg {
  fill: var(--sixth-color);
}

[data-theme="claudiondark"] .standard-sidebar-item:hover {
  background-color: var(--second-color);
}

[data-theme="claudiondark"] .standard-sidebar-item:hover a,
[data-theme="claudiondark"] .standard-sidebar-item:hover svg {
  color: var(--sixth-color);
  fill: var(--sixth-color);
}
[data-theme="claudiondark"] use[href="#es-line-sidebar-collapse"] {
  fill: var(--fourth-color) !important;
}

/* Navbar */
[data-theme="claudiondark"] .navbar {
  background-color: var(--second-color);
  border: none !important;
}

[data-theme="claudiondark"] .navbar-nav > li > a {
  color: var(--sixth-color) !important;
}

[data-theme="claudiondark"] .navbar-nav > li > a:hover {
  color: var(--second-color) !important;
}

[data-theme="claudiondark"] .navbar-brand:hover {
  color: var(--second-color) !important;
}

[data-theme="claudiondark"] .sticky-top {
  background-color: var(--second-color) !important;
}

/* Widgets */

[data-theme="claudiondark"] .widget {
  background-color: var(--main-color);
  border-color: var(--main-color);
  border-radius: 10px;
}
[data-theme="claudiondark"] .widget-subtitle {
  color: var(--sixth-color) !important;
}
[data-theme="claudiondark"] .link-text {
  color: var(--fourth-color);
}

[data-theme="claudiondark"].link-text :hover {
  color: var(--main-color) !important;
}

[data-theme="claudiondark"]
  .widget.shortcut-widget-box
  .widget-head
  .widget-title
  i {
  color: var(--sixth-color);
}

[data-theme="claudiondark"]
  .widget.shortcut-widget-box
  .widget-head
  .widget-title:hover
  span {
  color: var(--second-color);
}

[data-theme="claudiondark"] .widget-body .link-item:hover {
  color: var(--main-color);
  background: var(--third-color);
}

[data-theme="claudiondark"] span.ellipsis {
  color: var(--second-color) !important;
}

[data-theme="claudiondark"] .widget.spacer {
  background-color: var(--second-color);
}

[data-theme="claudiondark"] .h4 {
  color: var(--main-color) !important;
}
/* Sidebar */
[data-theme="claudiondark"] .desk-sidebar .desk-sidebar-item {
  font-size: 13px;
  font-weight: 600;
  color: var(--sixth-color);
}

[data-theme="claudiondark"] .layout-side-section {
  background-color: var(--main-color);
  font-size: 14px;
  border-radius: 10px;
  color: var(--sixth-color);
}

[data-theme="claudiondark"] .uppercase {
  color: var(--sixth-color);
}

/* Dropdowns */
[data-theme="claudiondark"] .dropdown-menu > li > a {
  font-size: 13px;
  color: var(--fifth-color);
  font-weight: 500;
}

[data-theme="claudiondark"] .awesomplete > ul > li:hover {
  background: var(--second-color);
}

[data-theme="claudiondark"] .awesomplete > ul > li:hover a {
  color: var(--sixth-color);
}

/* Page Heading */
[data-theme="claudiondark"] .page-head {
  background-color: var(--main-color);
  border-bottom: none !important;
}

[data-theme="claudiondark"] .page-title .title-text {
  color: var(--second-color);
  font-size: 22px;
}

[data-theme="claudiondark"] .section-head {
  color: var(--main-color);
}

[data-theme="claudiondark"] .widget .widget-head,
[data-theme="claudiondark"] .comment-input-header {
  font-size: 14px;
  color: var(--sixth-color);
}
[data-theme="claudiondark"] .widget-title {
  color: var(--second-color);
}
/* Buttons */
[data-theme="claudiondark"] .btn-primary {
  background-color: var(--second-color);
  border-color: var(--second-color);
}
[data-theme="claudiondark"] .btn-secondary {
  background-color: var(--second-color);
  border-color: var(--second-color);
}

[data-theme="claudiondark"] .btn-secondary:hover {
  background-color: var(--third-color);
  color: var(--main-color);
}

[data-theme="claudiondark"] .btn-default:hover,
[data-theme="claudiondark"] .btn-default:focus,
[data-theme="claudiondark"] .btn-default:active,
[data-theme="claudiondark"] .open > .dropdown-toggle.btn-default {
  background-color: var(--third-color) !important;
  color: var(--main-color) !important;
}
[data-theme="claudiondark"] .link-content.ellipsis:hover .link-text {
  color: var(--second-color);
}

/* Indicators */
[data-theme="claudiondark"] .indicator-right.blue:after,
.indicator.blue:before {
  background: var(--third-color);
}

/* Other Components */
[data-theme="claudiondark"] input[type="checkbox"]:checked:before {
  color: var(--third-color);
}

[data-theme="claudiondark"] input[type="checkbox"]:checked {
  background: var(--main-color);
}

[data-theme="claudiondark"] #page-Workspaces .page-head {
  background: var(--main-color);
}
